// useSpeechSynthesis.js
import { useRef, useCallback } from 'react';

export const useSpeechSynthesis = () => {
    const currentUtteranceRef = useRef(null);

    // Fonction pour annuler toute lecture en cours
    const cancelAllSpeech = useCallback(() => {
        if (window.speechSynthesis) {
            // Annuler toute synthse vocale en cours
            window.speechSynthesis.cancel();
        }
        
        // Rinitialiser la rfrence
        currentUtteranceRef.current = null;
    }, []);

    // Fonction pour lire du texte
    const speakText = useCallback((text, onEndCallback = null) => {
        // Vrifier que l'API est disponible
        if (!window.speechSynthesis) {
            console.error('Speech synthesis not supported');
            return;
        }

        // Annuler toute lecture prcdente pour viter les chevauchements
        cancelAllSpeech();

        // Crer une nouvelle utterance
        const utterance = new SpeechSynthesisUtterance(text);
        
        // Configurer la langue (franais)
        utterance.lang = 'fr-FR';
        
        // Ajouter le callback de fin si fourni
        if (onEndCallback) {
            utterance.onend = () => {
                // S'assurer que l'vnement n'est appel qu'une seule fois
                utterance.onend = null;
                
                // Supprimer la rfrence une fois termine
                if (currentUtteranceRef.current === utterance) {
                    currentUtteranceRef.current = null;
                }
                
                // Appeler le callback fourni
                onEndCallback();
            };
            
            // Grer galement les erreurs pour viter les blocages
            utterance.onerror = () => {
                utterance.onend = null;
                utterance.onerror = null;
                
                if (currentUtteranceRef.current === utterance) {
                    currentUtteranceRef.current = null;
                }
                
                onEndCallback();
            };
        }
        
        // Stocker la rfrence  cette utterance
        currentUtteranceRef.current = utterance;
        
        // Dmarrer la lecture
        window.speechSynthesis.speak(utterance);
    }, [cancelAllSpeech]);

    return {
        speakText,
        cancelAllSpeech,
        currentUtteranceRef
    };
};