import { useEffect, useRef } from 'react';

export const useTimer = ({
    isActive,
    timeLeft,
    setTimeLeft,
    isNavigatingRef,
    isTransitioning,
    timerRef,
    onTimeUpdate,
    onTimeUp
}) => {
    // Rfrence pour viter les appels multiples  onTimeUp
    const hasCalledTimeUpRef = useRef(false);
    
    useEffect(() => {
        // Rinitialiser la rfrence lorsque timeLeft change de 0  une valeur positive
        if (timeLeft > 0) {
            hasCalledTimeUpRef.current = false;
        }
        
        // Nettoyer tout timer existant pour viter les chevauchements
        if (timerRef.current) {
            clearTimeout(timerRef.current);
            timerRef.current = null;
        }

        // Ne dmarrer le timer que si les conditions sont runies
        if (isActive && timeLeft > 0 && !isNavigatingRef.current && !isTransitioning) {
            timerRef.current = setTimeout(() => {
                // Double vrification que nous ne sommes pas en transition
                if (isNavigatingRef.current || isTransitioning) {
                    return;
                }
                
                setTimeLeft(prevTime => {
                    const newTime = prevTime - 1;
                    
                    // Si un handler de mise  jour du temps est fourni, l'appeler
                    if (onTimeUpdate && newTime > 0) {
                        onTimeUpdate(newTime);
                    }
                    
                    return newTime;
                });
            }, 1000);
        } else if (timeLeft === 0 && isActive && !isTransitioning && !isNavigatingRef.current && !hasCalledTimeUpRef.current) {
            // Assurer qu'on n'appelle onTimeUp qu'une seule fois
            hasCalledTimeUpRef.current = true;
            
            // Appeler onTimeUp  la fin de la queue d'excution
            setTimeout(() => {
                // Vrifier  nouveau les conditions avant d'appeler onTimeUp
                if (timeLeft === 0 && isActive && !isTransitioning && !isNavigatingRef.current) {
                    if (onTimeUp) {
                        onTimeUp();
                    }
                }
            }, 0);
        }

        // Nettoyage du timer lors du dmontage ou changement de dpendances
        return () => {
            if (timerRef.current) {
                clearTimeout(timerRef.current);
                timerRef.current = null;
            }
        };
    }, [
        isActive,
        timeLeft,
        setTimeLeft,
        isNavigatingRef,
        isTransitioning,
        timerRef,
        onTimeUpdate,
        onTimeUp
    ]);
};