import React, { useState, useEffect, useRef } from 'react';
import {
    Panel,
    Button,
    ButtonGroup,
    FlexboxGrid,
    Content,
    Container,
    Header,
    Footer,
    Form,
    Divider,
    Progress
} from 'rsuite';
import 'rsuite/dist/rsuite.min.css';

// Importation des composants externaliss
import QuestionDisplay from './QuestionDisplay';
import ConfigScreen from './ConfigScreen';
import ResultsScreen from './ResultsScreen';

// Importation des hooks personnaliss
import { useSpeechSynthesis } from './hooks/useSpeechSynthesis';
import { useTimer } from './hooks/useTimer';

// Importation des utilitaires
import { calculateScore } from './utils/quizUtils';

import './QCMApp.css';

const QCMApp = ({ questions, questionReadCount = 1, answerTime = 3, allowMultipleAnswers = false }) => {
    // tats du composant
    const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
    const [selectedAnswers, setSelectedAnswers] = useState({});
    const [timeLeft, setTimeLeft] = useState(answerTime);
    const [isTimerActive, setIsTimerActive] = useState(false);
    const [audioPlayCount, setAudioPlayCount] = useState(0);
    const [showResults, setShowResults] = useState(false);
    const [quizStarted, setQuizStarted] = useState(false);
    const [quizFinished, setQuizFinished] = useState(false);
    const [showConfiguration, setShowConfiguration] = useState(true);
    const [isTransitioning, setIsTransitioning] = useState(false);
    const [audioCompleted, setAudioCompleted] = useState(false);
    const [config, setConfig] = useState({
        questionReadCount: questionReadCount,
        answerTime: answerTime,
        allowMultipleAnswers: allowMultipleAnswers
    });
    
    // Rfrences
    const timerRef = useRef(null);
    const isNavigatingRef = useRef(false);
    
    // Hooks personnaliss
    const { cancelAllSpeech, speakText, currentUtteranceRef } = useSpeechSynthesis();
    
    // La question actuelle
    const currentQuestion = questions[currentQuestionIndex];
    
    // Vrifier s'il s'agit de la dernire question
    const isLastQuestion = currentQuestionIndex === questions.length - 1;

    // Initialisation du suivi des annonces pour chaque question
    useEffect(() => {
        // Crer un objet pour suivre les annonces dj faites
        for (let i = 0; i < questions.length; i++) {
            window[`timer_announced_${i}`] = false;
        }
        
        // Nettoyer ces objets lors du dmontage du composant
        return () => {
            for (let i = 0; i < questions.length; i++) {
                delete window[`timer_announced_${i}`];
            }
        };
    }, [questions.length]);

    // Fonction pour dmarrer le quiz
    const startQuiz = () => {
        setQuizStarted(true);
        setShowConfiguration(false);
        prepareQuestion();
    };

    // Fonction pour prparer une nouvelle question
    const prepareQuestion = () => {
        // S'assurer que toute synthse vocale prcdente est bien arrte
        cancelAllSpeech();
        
        setTimeLeft(config.answerTime);
        setIsTimerActive(false);
        setAudioPlayCount(0);
        setAudioCompleted(false);

        // Effacer les marqueurs d'annonces prcdents pour cette question
        const questionKey = `timer_announced_${currentQuestionIndex}`;
        window[questionKey] = false;
    };

    // Fonction pour dmarrer le minuteur
    const startTimer = () => {
        // Ne pas dmarrer le timer si nous sommes en transition
        if (isNavigatingRef.current || isTransitioning) {
            return;
        }
        
        setIsTimerActive(true);
        
        // On utilise une rfrence pour suivre si l'annonce a dj t faite
        const questionKey = `timer_announced_${currentQuestionIndex}`;
        if (!window[questionKey]) {
            // Annonce vocale que le temps commence
            speakText(`Vous avez ${config.answerTime} secondes pour rpondre.`);
            
            // Marquer cette annonce comme dj faite pour cette question
            window[questionKey] = true;
        }
    };

    // Hook pour grer le minuteur
    useTimer({
        isActive: isTimerActive,
        timeLeft,
        setTimeLeft,
        isNavigatingRef,
        isTransitioning,
        timerRef,
        onTimeUpdate: (newTime) => {
            // Annonces vocales  certains moments
            if (newTime === 10) {
                if (!isNavigatingRef.current && !isTransitioning) {
                    cancelAllSpeech();
                    speakText("Plus que 10 secondes.");
                }
            } else if (newTime === 5) {
                if (!isNavigatingRef.current && !isTransitioning) {
                    cancelAllSpeech();
                    speakText("5 secondes restantes.");
                }
            }
        },
        onTimeUp: () => {
            // Marquer comme tant en transition pour viter les doubles notifications
            setIsTransitioning(true);
            
            // Annuler toute lecture en cours
            cancelAllSpeech();
            
            // Configurer le callback pour la fin de l'annonce
            const onEnd = () => {
                // Vrifier que nous ne sommes pas dj en cours de navigation
                if (!isNavigatingRef.current) {
                    // Passer  la question suivante seulement aprs que l'annonce soit termine
                    handleNextQuestion();
                }
            };
            
            // Temps coul
            speakText("Temps coul!", onEnd);
        }
    });

    // Nettoyer tous les events audio lors du dmontage du composant
    useEffect(() => {
        return () => {
            // Faire un nettoyage complet lors du dmontage
            cancelAllSpeech();
            if (timerRef.current) {
                clearTimeout(timerRef.current);
            }
        };
    }, [cancelAllSpeech]);

    // Grer le changement de slection
    const handleSelectionChange = (value) => {
        if (config.allowMultipleAnswers) {
            setSelectedAnswers(prev => ({
                ...prev,
                [currentQuestionIndex]: Array.isArray(value) ? value : [value]
            }));
        } else {
            // Si value est null ou undefined, on enregistre un tableau vide
            setSelectedAnswers(prev => ({
                ...prev,
                [currentQuestionIndex]: value !== null && value !== undefined ? [value] : []
            }));
        }
    };

    // Version amliore de la fonction pour passer  la question suivante
    const handleNextQuestion = () => {
        // Dfinir l'tat de navigation immdiatement pour bloquer tout traitement audio en cours
        isNavigatingRef.current = true;
        setIsTransitioning(true);
        
        // Arrter le timer si actif
        if (timerRef.current) {
            clearTimeout(timerRef.current);
            timerRef.current = null;
        }
        
        // Arrter compltement toute synthse vocale
        cancelAllSpeech();
        
        setIsTimerActive(false);
        
        // Effacer les marqueurs d'annonce pour TOUTES les questions
        for (let i = 0; i < questions.length; i++) {
            window[`timer_announced_${i}`] = false;
        }
        
        // Utiliser un timeout pour s'assurer que les vnements audio sont bien nettoys avant de continuer
        setTimeout(() => {
            if (isLastQuestion) {
                // Fin du quiz
                setQuizFinished(true);
                setShowResults(true);
                isNavigatingRef.current = false; // Rinitialiser l'tat de navigation
                setIsTransitioning(false);
            } else {
                // Passer  la question suivante 
                setCurrentQuestionIndex(prevIndex => prevIndex + 1);
                
                // Rinitialiser l'tat audio pour la nouvelle question
                setAudioPlayCount(0);
                setAudioCompleted(false);
                
                // Attendre un peu avant de rinitialiser les drapeaux de navigation
                setTimeout(() => {
                    // Rinitialiser les tats de navigation
                    isNavigatingRef.current = false;
                    setIsTransitioning(false);
                    
                    // Prparer la nouvelle question
                    prepareQuestion();
                }, 300);
            }
        }, 100);
    };

    // Redmarrer le quiz
    const restartQuiz = () => {
        // Dfinir l'tat de navigation pour bloquer tout traitement audio
        isNavigatingRef.current = true;
        
        // Annuler toute lecture en cours
        cancelAllSpeech();
        
        // Arrter le timer si actif
        if (timerRef.current) {
            clearTimeout(timerRef.current);
            timerRef.current = null;
        }
        
        setCurrentQuestionIndex(0);
        setSelectedAnswers({});
        setTimeLeft(config.answerTime);
        setIsTimerActive(false);
        setAudioPlayCount(0);
        setShowResults(false);
        setQuizFinished(false);
        setShowConfiguration(true);
        setQuizStarted(false);
        setIsTransitioning(false);
        
        // Rinitialiser l'tat de navigation aprs un court dlai
        setTimeout(() => {
            isNavigatingRef.current = false;
        }, 200);
    };

    // Mettre  jour la configuration
    const updateConfig = (key, value) => {
        setConfig(prev => ({
            ...prev,
            [key]: value
        }));
    };

    // Rendu principal avec design amlior
    return (
        <div className="qcm-container modern-theme">
            {showConfiguration && 
                <ConfigScreen 
                    config={config} 
                    updateConfig={updateConfig} 
                    startQuiz={startQuiz} 
                />
            }

            {quizStarted && !quizFinished && !showResults && (
                <Container className="quiz-main-container">
                    <Header className="quiz-header">
                        <FlexboxGrid justify="space-between" align="middle">
                            <FlexboxGrid.Item colspan={16}>
                                <div className="question-progress">
                                    <h2>Question {currentQuestionIndex + 1}</h2>
                                    <div className="progress-indicator">
                                        {questions.map((_, index) => (
                                            <div 
                                                key={index} 
                                                className={`progress-dot ${index === currentQuestionIndex ? 'active' : ''} 
                                                           ${index < currentQuestionIndex ? 'completed' : ''}`}
                                            />
                                        ))}
                                    </div>
                                </div>
                            </FlexboxGrid.Item>
                            <FlexboxGrid.Item colspan={8} style={{ textAlign: 'right' }}>
                                <div className="timer-container">
                                    <Progress.Circle
                                        percent={(timeLeft / config.answerTime) * 100}
                                        showInfo={false}
                                        status={
                                            timeLeft > config.answerTime * 0.5 ? 'success' :
                                                timeLeft > config.answerTime * 0.25 ? 'active' : 'fail'
                                        }
                                        strokeWidth={8}
                                        strokeColor={timeLeft > config.answerTime * 0.5 ? '#4CAF50' : 
                                                   timeLeft > config.answerTime * 0.25 ? '#FFC107' : '#F44336'}
                                        style={{ width: 60, marginRight: 15 }}
                                    />
                                    <span className={`timer-text ${timeLeft <= 10 ? 'timer-alert' : ''}`}>
                                        {timeLeft}s
                                    </span>
                                </div>
                            </FlexboxGrid.Item>
                        </FlexboxGrid>
                    </Header>

                    <Content className="quiz-content">
                        <Panel bordered className="question-panel">
                            <QuestionDisplay 
                                question={currentQuestion}
                                currentQuestionIndex={currentQuestionIndex}
                                selectedAnswers={selectedAnswers[currentQuestionIndex] || []}
                                allowMultipleAnswers={config.allowMultipleAnswers}
                                onSelectionChange={handleSelectionChange}
                                // Nouvelles props pour grer l'audio
                                speakText={speakText}
                                cancelAllSpeech={cancelAllSpeech}
                                questionReadCount={config.questionReadCount}
                                audioPlayCount={audioPlayCount}
                                setAudioPlayCount={setAudioPlayCount}
                                isNavigatingRef={isNavigatingRef}
                                isTransitioning={isTransitioning}
                                setAudioCompleted={setAudioCompleted}
                                startTimer={startTimer}
                            />
                        </Panel>
                    </Content>

                    <Footer className="quiz-footer">
                        <Button
                            appearance="primary"
                            className={`next-button ${isLastQuestion ? 'finish-button' : ''}`}
                            block
                            onClick={handleNextQuestion}
                            disabled={!audioCompleted || isTransitioning || isNavigatingRef.current}
                        >
                            {isLastQuestion ? "Terminer le quiz" : "Question suivante"}
                        </Button>
                    </Footer>
                </Container>
            )}

            {showResults && 
                <ResultsScreen 
                    questions={questions} 
                    selectedAnswers={selectedAnswers}
                    restartQuiz={restartQuiz}
                    speakText={speakText}
                    cancelAllSpeech={cancelAllSpeech}
                    isNavigatingRef={isNavigatingRef}
                    isTransitioning={isTransitioning}
                />
            }
        </div>
    );
};

export default QCMApp;