// src/contexts/ThemeContext.js
import React, { createContext, useContext, useState, useEffect } from 'react';

// Crer le contexte avec une valeur par dfaut
const ThemeContext = createContext({
  theme: 'light',
  toggleTheme: () => {}
});

// Crer le provider
export const ThemeProvider = ({ children }) => {
  const [theme, setTheme] = useState(() => {
    // Rcupration du thme depuis localStorage au dmarrage
    const savedTheme = localStorage.getItem('theme') || 'light';
    
    // Appliquer le thme initial au body
    document.body.classList.remove('light', 'dark');
    document.body.classList.add(savedTheme);
    
    return savedTheme;
  });

  // Mettre  jour le localStorage et le body quand le thme change
  useEffect(() => {
    // Mettre  jour localStorage
    localStorage.setItem('theme', theme);
    
    // Mettre  jour la classe du body
    document.body.classList.remove('light', 'dark');
    document.body.classList.add(theme);
  }, [theme]);

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === 'light' ? 'dark' : 'light'));
  };

  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
      {children}
    </ThemeContext.Provider>
  );
};

// Hook pour utiliser le contexte
export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useTheme must be used within a ThemeProvider');
  }
  return context;
};