import React, { useEffect } from 'react';
import { Container, Header, Content, Footer, Panel, Progress, Divider, Message, Button } from 'rsuite';
import { calculateScore } from './utils/quizUtils';

const ResultsScreen = ({
    questions,
    selectedAnswers,
    restartQuiz,
    speakText,
    cancelAllSpeech,
    isNavigatingRef,
    isTransitioning
}) => {
    const result = calculateScore(questions, selectedAnswers);
    const resultText = `Vous avez obtenu ${result.score} bonnes rponses sur ${result.total} questions (${result.percentage}%).`;

    // Effet pour lire le rsultat  voix haute
    useEffect(() => {
        // S'assurer que toute synthse vocale prcdente est termine
        cancelAllSpeech();

        // Lire le rsultat aprs un court dlai
        const timer = setTimeout(() => {
            // Vrifier que nous ne sommes pas en transition
            if (!isNavigatingRef.current && !isTransitioning) {
                speakText(resultText);
            }
        }, 500);

        return () => clearTimeout(timer);
    }, [cancelAllSpeech, speakText, resultText, isNavigatingRef, isTransitioning]);

    return (
        <Container className="results-container">
            <Header>
                <h2>Rsultats du QCM</h2>
            </Header>
            <Content>
                <Panel bordered>
                    <h3>Score final</h3>
                    <Progress.Circle
                        percent={result.percentage}
                        strokeColor={result.percentage >= 70 ? '#4CAF50' : result.percentage >= 50 ? '#FF9800' : '#F44336'}
                        showInfo={true}
                        status={result.percentage >= 70 ? 'success' : result.percentage >= 50 ? 'active' : 'fail'}
                    />
                    <Divider />
                    <p className="result-text">{resultText}</p>

                    <Divider />

                    <h3>Dtail des rponses</h3>
                    {questions.map((q, qIndex) => {
                        const userAnswers = selectedAnswers[qIndex] || [];
                        const isCorrect = Array.isArray(q.correctAnswer)
                            ? q.correctAnswer.every(ans => userAnswers.includes(ans)) && userAnswers.every(ans => q.correctAnswer.includes(ans))
                            : userAnswers.includes(q.correctAnswer);

                        return (
                            <div key={q.id} className="question-result">
                                <p className="question-text">
                                    <b>Question {qIndex + 1}:</b> {q.question}
                                </p>
                                <Message
                                    type={isCorrect ? 'success' : 'error'}
                                    showIcon
                                >
                                    {isCorrect ? 'Correct' : 'Incorrect'}
                                </Message>
                                <p>
                                    <b>Votre rponse:</b> {userAnswers.map(idx => q.options[idx]).join(', ') || 'Aucune rponse'}
                                </p>
                                <p>
                                    <b>Rponse correcte:</b> {
                                        Array.isArray(q.correctAnswer)
                                            ? q.correctAnswer.map(idx => q.options[idx]).join(', ')
                                            : q.options[q.correctAnswer]
                                    }
                                </p>
                                {q.explanation && (
                                    <p className="explanation"><b>Explication:</b> {q.explanation}</p>
                                )}
                                <Divider />
                            </div>
                        );
                    })}
                </Panel>
            </Content>
            <Footer>
                <Button appearance="primary" block onClick={restartQuiz}>
                    Recommencer le quiz
                </Button>
            </Footer>
        </Container>
    );
};

export default ResultsScreen;